
import app from './_firebase';

import axios from 'axios';
import { API_URL } from '../config';

export const listAllProducts = async () => {
  try {
    let querySnapshot = await app.firestore().collection('products').get();
    if (querySnapshot.docs) {
      const products = querySnapshot.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data()
        }
      })
      return products
    }
  } catch (err) {
    console.log(err);
  }
}


export const addProduct = async (data,courses) => {
  try { 
    data.courses = [];
    for await (let id of courses){
      let querySnapshot = await app.firestore().collection('courses').doc(id).get();
      const course = querySnapshot.data()
      let c={id:id,name:course.name,shortname:course.shortname};
      data.courses.push(c);
    }
    if(data.courses.length<1) {
      return;
    }
    let result  = await app.firestore().collection('products').add(data);
    const url = `${API_URL}/api/create_cohorts`;
    axios.post(url,data).then(resp => {
        console.log(resp.data);
    });

  } catch (err) {
      console.log(err);
  }
};

export const getProduct = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('product').doc(id).get();
    const product = querySnapshot.data()
    return product
  } catch (err) {
    console.log(err);
  }
}